:root {
  --mono-blue: #008FFF;
  --black: #333;
  --yellow: #FDFB53;
}
body {
  font-family: 'Montserrat', sans-serif;
  padding: 0;
  margin: 0;/*todo*/
  /* overflow: hidden; */
  display: flex;
  flex-flow: column;
}

.main > * {
  max-width: 712px;
  margin-left: 16px;
  margin-right: 16px;
}
.main {
  min-height: calc(100vh - 67px - 20px);
  display: flex;
  flex-flow: column;
  align-items: center;
  margin-top: 67px;
}

.article > p, .main > p, .p {
  font-size: 18px;
  /* line-height: 28px; */
  line-height: 24px;
  margin-bottom: 32px;
  margin-top: 32px;
  font-weight: 300;
  letter-spacing: 0.5px;
}
.footer > a {
  margin-right: 16px;
  font-size: 1rem;
  font-weight: 900;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  flex: 1;
  display: flex;
  justify-content: center;
}
.footer {
  height: 20px;
  align-items: center;
  overflow-x: hidden;
  margin-left: -40px;
  background-color: var(--black);
  width: calc(100vw + 40px);
  /* position:fixed;
  bottom: 0; */

  display: flex;
  overflow-x: hidden;
}
@media screen and (max-width: 400px) {
  .footer {
      padding-bottom: 20px;
  }
}
.sticky:nth-child(odd) {
  color: var(--yellow);
}
.sticky:nth-child(even) {
  color: white;
}

.blog {
  background-color: white;
  padding-bottom: 100px;
  width: 100vw;
}
.header {
  position: fixed;
  background-color: var(--yellow);
  padding-left: 8px;
  /* margin-left: -24px; */
}
.header, .header > div {
  display: flex;
  flex-flow: row;
}
.unorsk,  .design {
  margin-right: 20px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.unorsk {
  background: url('../assets/unorskheader.svg');
  display: block;
  width: 142px;
  height: 25px;
  background-position: center;
  background-size: contain;
}
.design {
  background: url('../assets/designheader.svg');
  display: block;
  width: 120px;
  height: 25px;
  background-position: center;
  background-size: contain;
}

.article-link:hover > h2 {
  /* background-color: var(--yellow); */
  text-decoration: underline;
}
.article-link {
  margin-bottom: 30px;
  text-decoration: none;
  color: var(--black);
}

h2 {
  margin-top: 0px;
  margin-bottom: 0;
  font-size: 50px;
  line-height: 50px;
  letter-spacing: -0.5px;
  font-weight: 300;
}
.article > .date {
  margin-bottom: 32px;
}

.date {
  font-size: 12px;
  line-height: 25px;
  font-weight: 200;
}